.textOne {
  font-size: 20px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .textOne {
    font-size: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .textOne {
    font-size: 35px;
  }
}

.textTwo {
  font-size: 17px;
  font-weight: 700;
  margin-top: 20px;
  color: blue;
}

@media screen and (min-width: 768px) {
  .textTwo {
    font-size: 25px;
    margin-top: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .textTwo {
    font-size: 28px;
    margin-top: 55px;
  }
}

.home {
  min-height: 100vh;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  background-image: linear-gradient(
    to bottom,
    rgba(31, 31, 244, 0.5),
    rgba(214, 210, 210, 0.5)
  );
}

@media screen and (min-width: 768px) {
  .home {
    padding-top: 70px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .home {
    padding-top: 100px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
